<template>
  <div class="shopWrap">
    <section class="shop_container">
      <nav class="goback">
        <i class="el-icon-arrow-left" style="color:white" @click="goback"></i>
      </nav>
      <div class="header">
        <div class="header_cover_img_con">
          <img :src="imgurl +imgurl2" alt />
        </div>
        <div class="description_header">
          <router-link to>
            <section class="description_left">
              <img :src="imgurl +imgurl2" alt />
            </section>
            <section class="description_right">
              <h4 class="description_title ellipsis">{{shopName}}</h4>
              <p class="description_text">商家配送／分钟送达／配送费¥5</p>
              <p class="description_promotion ellipsis">公告：欢迎光临，用餐高峰请提前下单，谢谢</p>
            </section>
            <i class="el-icon-arrow-right" style="color:white"></i>
          </router-link>
        </div>
      </div>
      <section class="change_show_type">
        <div>
          <span @click="toshangpin">商品</span>
        </div>
        <div>
          <span @click="topingjia">评价</span>
        </div>
      </section>
      <section class="food_container" v-if="isShow">
        <section class="menu_container">
          <!-- 导航 -->
          <section id="wrapper_menu" class="menu_left">
            <ul>
              <li
                class="menu_left_li"
                :class="active == index ? 'activity_menu' : '' "
                v-for="(s,index) in shopArr"
                :key="s.id"
                @click="goright(index)"
              >
              <!-- 点击动画 -->
                <el-badge :value="itemCount(s.id)" :hidden="itemCount(s.id) ? false : true">
                  <span>{{s.name}}</span>
                </el-badge>
              </li>
            </ul>
          </section>
          <!-- 商品 -->
          <section class="menu_right">
            <ul v-for="s in shopArr" :key="s.id">
              <li>
                <div class="menu_detail_header">
                  <section class="menu_detail_header_left">
                    <strong class="menu_item_title">{{s.name}}</strong>
                    <span class="menu_item_description">{{s.description}}～</span>
                  </section>
                  <span class="menu_detail_header_right">···</span>
                </div>
                <ul>
                  <li class v-for="(s2,index) in s.foods" :key="index">
                    <section class="menu_detail_list">
                      <div class="menu_detail_link">
                        <section class="menu_food_img">
                          <img :src="imgurl + s2.image_path" alt />
                        </section>
                        <section class="menu_food_description">
                          <h3 class="food_description_head">
                            <strong class="description_foodname">{{s2.name}}</strong>
                            <ul class="attributes_ul">
                              <li>招牌</li>
                            </ul>
                          </h3>
                          <p class="food_description_content">{{s2.description}}</p>
                          <p class="food_description_sale_rating">
                            <span>月售{{s2.month_sales}}份</span>
                            <span>好评率{{s2.satisfy_rate}}%</span>
                          </p>
                          <p class="food_activity">
                            <span>打威群岛</span>
                          </p>
                        </section>
                      </div>
                      <div class="menu_detail_footer">
                        <section class="food_price">
                          <span class="span1">¥</span>
                          <span class="span2">{{s2.specfoods[0].price}}</span>
                          <span class="span3">起</span>
                        </section>
                        <section class="cart_module">
                          <section class="choose_specification">
                            <section class="choose_icon_container">
                              <div class="cart_button">
                                <div v-if="foodNum(s2)">
                                  <span
                                    @click="s2.specifications.length?xggjianshao():jianshao(foodNum(s2),s2.category_id, s2.item_id, s2.specfoods[0].food_id, s2.specfoods[0].name, s2.specfoods[0].price, s2.specfoods[0].specs, s2.specfoods[0].packing_fee, s2.specfoods[0].sku_id, s2.specfoods[0].stock)"
                                  >
                                    <i class="el-icon-remove-outline"></i>
                                  </span>
                                  <span class="cart_num1">{{foodNum(s2)}}</span>
                                </div>
                              </div>
                              <span
                                class="show_chooselist"
                                v-if="s2.specifications.length?true:false"
                                @click="xuanguige(s2.specfoods,s2.category_id)"
                              >选规格</span>
                              <span class="add_icon" v-if="s2.specifications.length?false:true">
                                <i
                                  class="el-icon-circle-plus-outline"
                                  @click="tianjia(s2.category_id, s2.item_id, s2.specfoods[0].food_id, s2.specfoods[0].name, s2.specfoods[0].price, s2.specfoods[0].specs, s2.specfoods[0].packing_fee, s2.specfoods[0].sku_id, s2.specfoods[0].stock)"
                                ></i>
                              </span>
                            </section>
                            <div class="specs_list" v-if="xuanguigeIsshow">
                              <div class="specs_list_header">
                                <h4 class="ellipsis">{{xuanguigeName}}</h4>
                                <i
                                  class="specs_cancel el-icon-close"
                                  @click="closethis"
                                  style="font-size:1rem"
                                ></i>
                              </div>
                              <section class="specs_details">
                                <h5 class="specs_details_title">规格</h5>
                                <ul>
                                  <li
                                    class="specs_activity"
                                    :style="guigebgc==index?'color:blue;':''"
                                    v-for="(gg,index) in xuanguigeArr"
                                    :key="index"
                                    @click="guigeID(index)"
                                  >{{gg.specs[0].value}}</li>
                                </ul>
                              </section>
                              <div class="specs_footer">
                                <div class="specs_price">
                                  <span>¥</span>
                                  <span>{{guigeArr.price}}</span>
                                </div>
                                <div
                                  class="specs_addto_cart"
                                  @click="putCar(xuanguigecId, xuanguigeArr[guigebgc].item_id, xuanguigeArr[guigebgc].food_id, xuanguigeName, guigeArr.price, xuanguigeArr[guigebgc].specs[0].value, xuanguigeArr[guigebgc].packing_fee, xuanguigeArr[guigebgc].sku_id, xuanguigeArr[guigebgc].stock)"
                                >加入购物车</div>
                              </div>
                            </div>
                          </section>
                        </section>
                      </div>
                    </section>
                  </li>
                </ul>
              </li>
            </ul>
          </section>
        </section>
        <!-- 底部购物车 -->
        <section class="buy_cart_container">
          <section class="cart_icon_num">
            <div
              id="cart_icon_container"
              @click="showCar"
              :style="allFoodsPrice?'background-color:#3190e8':''"
            >
              <span class="cart_list_length" v-if="allFoodsPrice">{{allItemCount}}</span>
              <i class="el-icon-shopping-cart-1"></i>
            </div>
            <div class="cart_num">
              <div class="div1">¥ {{allFoodsPrice}}.00</div>
              <div class="div2">配送费¥5</div>
            </div>
          </section>
          <section
            id="gotopay"
            :style="allFoodsPrice?'background-color:#4cd964':''"
            @click="goDist"
          >
            <span id="gotopay_button_style">{{allFoodsPrice ? '去下单' : '还差¥20起送'}}</span>
          </section>
        </section>
      </section>
    <!-- 购物车展示 -->
      <el-collapse-transition>
        <section class="cart_food_list" v-if="carShow">
          <div class="carheader">
            <h4>购物车</h4>
            <div>
              <i></i>
              <span class="clear_cart" @click="clearAll">清空</span>
            </div>
          </div>
          <section id="cartFood" class="cart_food_details">
            <ul>
              <li class="cart_food_li" v-for="(good1,index) in carArr" :key="index">
                <div class="cart_list_num">
                  <p class="ellipsis">{{good1.name}}</p>
                  <p class="ellipsis" style="font-size:.5rem">{{good1.specs == ''?'':good1.specs}}</p>
                  <!-- <p class="ellipsis">{{}}</p> -->
                </div>
                <div class="cart_list_price">
                  <span>¥</span>
                  <span>{{good1.price}}</span>
                </div>
                <section class="cart_list_control">
                  <div class="cart_button" style="display:block">
                    <span>
                      <i
                        class="el-icon-remove-outline"
                        @click="jianshao1(good1.shopId,good1.category_id, good1.item_id, good1.food_id, good1.name, good1.price, good1.specs, good1.packing_fee, good1.sku_id, good1.stock)"
                      ></i>
                    </span>
                    <span class="cart_num1">{{good1.num}}</span>
                  </div>
                  <span class="add_icon">
                    <i
                      class="el-icon-circle-plus-outline"
                      @click="tianjia1(good1.shopId,good1.category_id, good1.item_id, good1.food_id, good1.name, good1.price, good1.specs, good1.packing_fee, good1.sku_id, good1.stock)"
                    ></i>
                  </span>
                </section>
              </li>
            </ul>
          </section>
        </section>
      </el-collapse-transition>
      <section v-if="!isShow">
        <div class="rating_header">
          <section class="rating_header_left">
            <p>{{shopArr4.overall_score}}</p>
            <p>综合评价</p>
            <p>高于周边商家{{shopArr4.compare_rating}}</p>
          </section>
          <section class="rating_header_right">
            <p>
              <span>服务态度</span>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-star-off"></i>
              <span class="rating_num">{{shopArr4.service_score}}</span>
            </p>
            <p>
              <span>菜品评价</span>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-star-off"></i>
              <span class="rating_num">{{shopArr4.food_score}}</span>
            </p>
            <p>
              <span>送达时间</span>
              <span class="delivery_time">{{shopArr4.deliver_time}}分钟</span>
            </p>
          </section>
        </div>
        <ul class="tag_list_ul">
          <li v-for="(s3,index) in shopArr3" :key="index">{{s3.name}}({{s3.count}})</li>
        </ul>
        <ul class="rating_list_ul">
          <li class="rating_list_li" v-for="(s5,index) in shopArr5" :key="index">
            <img :src="s5.avatar?imgurl4:morenImg" alt class="user_avatar" />
            <section class="rating_list_details">
              <div class="head">
                <section class="username_star">
                  <p class="username">{{s5.username}}</p>
                  <p class="star_desc">
                    <i class="el-icon-star-off"></i>
                    <i class="el-icon-star-off"></i>
                    <i class="el-icon-star-off"></i>
                    <i class="el-icon-star-off"></i>
                    <i class="el-icon-star-off"></i>
                    <span class="time_spent_desc">{{s5.time_spent_desc}}</span>
                  </p>
                </section>
                <div class="rated_at">{{s5.rated_at}}</div>
              </div>
              <ul class="food_img_ul">
                <li v-for="s5img in s5.item_ratings" :key="s5img.food_id">
                  <img :src="imgurl2 + s5img.image_hash +imgurl3" alt />
                </li>
              </ul>
              <ul class="food_name_ul">
                <li
                  class="ellipsis"
                  v-for="s6 in s5.item_ratings"
                  :key="s6.food_id"
                >{{s6.food_name}}</li>
              </ul>
            </section>
          </li>
        </ul>
      </section>
    </section>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Shop",
  data() {
    return {
      shopId: "",
      imgurl: "https://elm.cangdu.org/img/",
      shopName: "",
      shopArr: [],
      shopArr2: [],
      rightname: "热搜榜",
      righedes: "热销来袭",
      active: 0,
      shopArr3: [],
      shopArr4: [],
      shopArr5: [],
      imgurl2: "https://fuss10.elemecdn.com/d/",
      imgurl3: ".jepg",
      morenImg: "https://elm.cangdu.org/img/default.jpg",
      imgurl4: "https://fuss10.elemecdn.com/1/5f/",
      isShow: true,
      active1: 99999,
      shopCount: 0,
      priceAll: 0,
      countAll: 0,
      isShowcount: false,
      carShow: false,
      goodsIndex: [],
      xuanguigeIsshow: false,
      xuanguigeArr: [],
      guigeArr: [],
      guigebgc: 90,
      xuanguigeName: "",
      xuanguigecId: "",
      carArr: [],
      shopCarts: null
    };
  },
  created() {
    this.shopId = localStorage.getItem("shopId");
    this.imgurl2 = localStorage.getItem("shopImg");
    this.shopName = localStorage.getItem("shopName");
    this.axios
      .get("shopping/v2/menu", {
        params: {
          restaurant_id: this.shopId
        }
      })
      .then(res => {
        this.shopArr = res;
        // console.log(this.shopArr);
      })
      .catch(err => {});
    this.axios
      .get("/ugc/v2/restaurants/" + this.shopId + "/ratings/tags")
      .then(res => {
        //   console.log(res)
        this.shopArr3 = res;
      })
      .catch(err => {});
    this.axios
      .get("/ugc/v2/restaurants/" + this.shopId + "/ratings/scores")
      .then(res => {
        //   console.log(res)
        this.shopArr4 = res;
      })
      .catch(err => {});
    this.axios
      .get("/ugc/v2/restaurants/" + this.shopId + "/ratings")
      .then(res => {
        //   console.log(res)
        this.shopArr5 = res;
      })
      .catch(err => {});

    // 初始化vuex
    this.$store.commit("init_buy_cart");

       console.log(this.$store.state.cartList);

    // 处理购物车数据
    this.handleCartData();
  },
  watch: {
    shopCart: {
      handler(newV, oldV) {
        console.log("监听计算属性的变化,vuex数据发生变化");
        this.handleCartData();
      },
      deep: true
    }
  },
  methods: {
    handleCartData() {
      this.carArr = [];
      this.shopCarts = this.$store.state.cartList;
      Object.keys(this.shopCarts).forEach(shopId => {
        // console.log(this.shopCarts[shopId])
        Object.keys(this.shopCarts[shopId]).forEach(category_id => {
          Object.keys(this.shopCarts[shopId][category_id]).forEach(item_id => {
            Object.keys(this.shopCarts[shopId][category_id][item_id]).forEach(
              food_id => {
                let foodItem = this.shopCarts[shopId][category_id][item_id][
                  food_id
                ];
                let newFoodItem = {
                  ...foodItem,
                  category_id,
                  item_id,
                  food_id,
                  shopId
                };
                if (newFoodItem.shopId == this.shopId) {
                  this.carArr.push(newFoodItem);
                }
              }
            );
          });
        });
      });
    },
    goback() {
      this.$router.push("/food");
    },
    goright(i) {
      this.active = i;
      let selector = ".menu_right>ul:nth-child(" + (i + 1) + ")>li";
      this.$el.querySelector(selector).scrollIntoView({
        behavior: "smooth", // 平滑过渡
        block: "start" // 上边框与视窗顶部平齐。默认值
      });
    },
    toshangpin() {
      this.isShow = true;
    },
    topingjia() {
      this.isShow = false;
    },
    //普通添加
    tianjia(
      category_id,
      item_id,
      food_id,
      name,
      price,
      specs,
      packing_fee,
      sku_id,
      stock
    ) {
      this.$store.commit("add_cart", {
        shopid: this.shopId,
        category_id,
        item_id,
        food_id,
        name,
        price,
        specs,
        packing_fee,
        sku_id,
        stock
      });
    },
    //普通减少
    jianshao(
      i,
      category_id,
      item_id,
      food_id,
      name,
      price,
      specs,
      packing_fee,
      sku_id,
      stock
    ) {
      //console.log("减少", i, category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock);
        //调用vuex中的mutations中的方法传入添加商品的信息
      this.$store.commit("reduce_cart", {
        shopid: this.shopId,
        category_id,
        item_id,
        food_id,
        name,
        price,
        specs,
        packing_fee,
        sku_id,
        stock
      });
    },
    //选规格添加
    putCar(
      category_id,
      item_id,
      food_id,
      name,
      price,
      specs,
      packing_fee,
      sku_id,
      stock
    ) {
      // console.log(category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock)
      this.$store.commit("add_cart", {
        shopid: this.shopId,
        category_id,
        item_id,
        food_id,
        name,
        price,
        specs,
        packing_fee,
        sku_id,
        stock
      });
      this.xuanguigeIsshow = !this.xuanguigeIsshow;
    },
    //购物车列表添加
    tianjia1(
      shopId,
      category_id,
      item_id,
      food_id,
      name,
      price,
      specs,
      packing_fee,
      sku_id,
      stock
    ) {
      console.log(
        shopId,
        category_id,
        item_id,
        food_id,
        name,
        price,
        specs,
        packing_fee,
        sku_id,
        stock
      );
      this.$store.commit("add_cart", {
        shopid: shopId,
        category_id,
        item_id,
        food_id,
        name,
        price,
        specs,
        packing_fee,
        sku_id,
        stock
      });
    },
    //购物车列表减少
    jianshao1(
      shopId,
      category_id,
      item_id,
      food_id,
      name,
      price,
      specs,
      packing_fee,
      sku_id,
      stock
    ) {
      this.$store.commit("reduce_cart", {
        shopid: shopId,
        category_id,
        item_id,
        food_id,
        name,
        price,
        specs,
        packing_fee,
        sku_id,
        stock
      });
    },
    showCar() {
      this.carShow = !this.carShow;
    },
    clearAll() {
      this.$store.commit("delete_cart", { shopid: this.shopId });
      this.carShow = !this.carShow;
    },
    xuanguige(i, cId) {
      this.xuanguigeIsshow = !this.xuanguigeIsshow;
      // console.log(i)
      this.xuanguigeArr = i;
      // console.log(this.xuanguigeArr);
      this.xuanguigeName = i[0].name;
      this.xuanguigecId = cId;
      // console.log(this.xuanguigeName)
    },
    closethis() {
      this.xuanguigeIsshow = !this.xuanguigeIsshow;
    },
    guigeID(i) {
      // console.log(i)
      this.guigeArr = this.xuanguigeArr[i];
      // console.log(this.guigeArr)
      this.guigebgc = i;
    },
    xggjianshao() {
      alert("请去购物车列表操作");
    },
    goDist() {
      this.$router.push("/dist");
      // console.log(this.allFoodsPrice)
      localStorage.setItem('allprice',this.allFoodsPrice)
    }
  },
  computed: {
    //总价格
    allFoodsPrice() {
      let allFoodsCount = 0;
      if (this.shopCarts) {
        // let allFoods = this.$store.state.cartList;
        Object.keys(this.shopCarts).forEach(shopId => {
          Object.keys(this.shopCarts[shopId]).forEach(category_id => {
            Object.keys(this.shopCarts[shopId][category_id]).forEach(
              item_id => {
                Object.keys(
                  this.shopCarts[shopId][category_id][item_id]
                ).forEach(food_id => {
                  let foodItem = this.shopCarts[shopId][category_id][item_id][
                    food_id
                  ];
                  let newFoodItem = {
                    ...foodItem,
                    category_id,
                    item_id,
                    food_id,
                    shopId
                  };
                  if (newFoodItem.shopId == this.shopId) {
                    allFoodsCount += newFoodItem.num * newFoodItem.price;
                  }
                });
              }
            );
          });
        });
      }
      return allFoodsCount;
    },
    //购物车总数量
    allItemCount() {
      let allItemCount = 0;
      if (this.shopCarts) {
        // let allFoods = this.$store.state.cartList;
        // console.log(this.shopCarts)
        Object.keys(this.shopCarts).forEach(shopId => {
          Object.keys(this.shopCarts[shopId]).forEach(category_id => {
            Object.keys(this.shopCarts[shopId][category_id]).forEach(
              item_id => {
                Object.keys(
                  this.shopCarts[shopId][category_id][item_id]
                ).forEach(food_id => {
                  let foodItem = this.shopCarts[shopId][category_id][item_id][
                    food_id
                  ];
                  let newFoodItem = {
                    ...foodItem,
                    category_id,
                    item_id,
                    food_id,
                    shopId
                  };
                  if (newFoodItem.shopId == this.shopId) {
                    allItemCount += newFoodItem.num;
                  }
                });
              }
            );
          });
        });
      }
      return allItemCount;
    },
    //分类总数量
    itemCount() {
      return cateid => {
        let itemFoodCount = 0;
        if (this.shopCart && this.shopCart[cateid]) {
          Object.values(this.shopCart[cateid]).forEach(foods => {
            Object.values(foods).forEach(food => {
              // console.log(food);
              itemFoodCount += food.num;
            });
          });
        }
        return itemFoodCount;
      };
    },
    ...mapState(["cartList"]),
    //购物车
    shopCart: function() {
      return Object.assign({}, this.$store.state.cartList[this.shopId]);
    },
    //商品数量
    foodNum() {
      return foods => {
        let category_id = foods.category_id;
        let item_id = foods.item_id;
        let itemNum;
        if (
          this.shopCart &&
          this.shopCart[category_id] &&
          this.shopCart[category_id][item_id]
        ) {
          let num = 0;
          Object.values(this.shopCart[category_id][item_id]).forEach(
            (item, index) => {
              itemNum = item ? item.num : 0;
              num += itemNum;
            }
          );
          return itemNum;
        } else {
          return 0;
        }
